// Highlight backgrounds

.hl-default {
  background-color: @state-default-bg;
  a&:hover {
    background-color: darken(@state-default-bg, 10%);
  }
}
.hl-primary {
  background-color: @state-primary-bg;
  a&:hover {
    background-color: darken(@state-primary-bg, 10%);
  }
}
