.img-responsive,
.carousel-inner > .item > img,
.carousel-inner > .item > a > img {
  display: block;
  max-width: 100%;
  height: auto;
}
.img-rounded {
  border-radius: 3px;
}
.img-thumbnail {
  padding: 4px;
  line-height: 1.53846154;
  background-color: #fff;
  border: 1px solid #dddddd;
  border-radius: 2px;
  transition: all 0.2s ease-in-out;
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.img-circle {
  border-radius: 50%;
}
.nav-primary > li > a > .label,
.btn .label {
  position: relative;
  top: -1px;
}
.btn-primary .label,
.btn-success .label,
.btn-info .label,
.btn-danger .label,
.btn-warning .label {
  background-color: rgba(0, 0, 0, 0.2);
}
.list-group-item.active > .label,
.nav-primary > .active > a > .label,
.nav-pills > .active > a > .label {
  color: #0288d1;
  background-color: #fff;
}
.nav > li > a > .label {
  margin-left: 3px;
}
.label {
  display: inline;
  padding: .2em .6em .2em;
  font-size: 75%;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  border-radius: .25em;
  background-color: #808080;
}
.label[href]:hover,
.label[href]:focus {
  color: #fff;
  background-color: #666666;
}
.label.label-circle {
  background: none;
  border: 1px solid #808080;
  color: #808080;
}
a.label:hover,
a.label:focus {
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}
.label:empty {
  display: none;
}
.label-badge {
  display: inline-block;
  border-radius: 12px;
  min-width: 18px;
  padding: 3px 7px;
  font-size: 12px;
  line-height: 1;
}
.label-dot {
  text-indent: -9999em;
  display: inline-block;
  padding: 0px;
  border-radius: 5px;
  width: 10px;
  height: 10px;
  line-height: 13px;
}
.label-dot:empty {
  display: inline-block;
}
.label-primary {
  background-color: #3498db;
}
.label-primary[href]:hover,
.label-primary[href]:focus {
  color: #fff;
  background-color: #217dbb;
}
.label-primary.label-circle {
